<template>
	<div class="productList-container" ref="productList">
		<!--  -->
		<Search @submit="handleSubmit" :categoryList="categoryList" />
		<a-button class="productAdd">
			<router-link :to="{ name: 'ProductAdd' }">  </router-link>
		</a-button>
		<!--  -->
		<ProductTable
			:data="productTable"
			:page="page"
			@changePage="handleChangePage"
			@edit="handleEditProduct"
			@remove="handleRemoveProduct"
		/>
	</div>
</template>

<script>
import Search from '@/components/search.vue';
import ProductTable from '@/components/productTable.vue';
import categoryApi from '@/api/category';
import productApi from '@/api/product';

export default {
	components: {
		Search,
		ProductTable,
	},
	data() {
		return {
			searchForm: {},
			productTable: [],
			categoryList: [],
			categoryObj: {}, // item category { item.id: item.name  }
			page: {
				current: 1,
				pageSize: 10,
				showSizeChanger: true,
				total: 1,
			},
		};
	},
	created() {
		categoryApi.getCategoryList().then((res) => {
			this.categoryList = res.data.data;
			this.categoryList.forEach((item) => {
				this.categoryObj[item.id] = item.name;
			});
		});
		this.getTableData();
	},
	methods: {
		handleSubmit(searchForm) {
			this.searchForm = searchForm;
			this.getTableData();
		},
		getTableData() {
			productApi.getProductTable({
					page: this.page.current,
					size: this.page.pageSize,
					...this.searchForm,
				})
				.then((res) => {
					this.productTable = res.data.data.map((item) => ({
						...item,
						categoryName: this.categoryObj[item.category],
					}));
					this.page.total = res.data.total;
				});
		},
		// 
		handleChangePage(page) {
			this.page = page;
			this.getTableData();
			document.documentElement.scrollTop = 0;
		},
		// 
		handleEditProduct(record) {
			this.$router.push({
				name: 'ProductEdit',
				params: {
					id: record.id,
				},
			});
		},
		handleRemoveProduct(record) {
			this.$confirm({
				title: '',
				content: () => <div style="color:red;">{record.title}</div>,
				onOk: () => {
					productApi.removeProduct(record.id).then((res) => {
						this.getTableData();
						this.$message.success(`${res.msg}!`);
					}).catch((error) => {
						this.$message.error(`${error.msg}!`);
					});
				},
				onCancel() {},
				class: 'removeProduct',
			});
		},
	},
};
</script>

<style lang="less" scoped>
.productList-container {
	position: relative;

	.productAdd {
		position: absolute;
		right: 50px;
		top: 4px;
	}
}
</style>
