<template>
	<a-table
	:columns="columns"
	:data-source="tableData"
	:pagination="page"
	@change="changePage"
	>
		<div slot="operation" slot-scope="text, record">
			<a-button @click="editProduct(record)"></a-button>
			<a-button @click="removeProduct(record)"></a-button>
		</div>
	</a-table>
</template>

<script>
const columns = [
	{
		title: 'ID',
		dataIndex: 'id',
		key: 'id',
	},
	{
		title: '',
		dataIndex: 'title',
		key: 'title',
		ellipsis: true,
	},
	{
		title: '',
		dataIndex: 'desc',
		key: 'desc',
		ellipsis: true,
	},
	{
		title: '',
		dataIndex: 'categoryName',
		key: 'category',
		ellipsis: true,
	},
	{
		title: '',
		dataIndex: 'price',
		key: 'price',
	},
	{
		title: '',
		dataIndex: 'price_off',
		key: 'price_off',
	},
	{
		title: '',
		dataIndex: 'tags',
		key: 'tags',
		ellipsis: true,
	},
	{
		title: '',
		dataIndex: 'inventory',
		key: 'inventory',
	},
	{
		title: '',
		dataIndex: 'status',
		key: 'status',
		customRender: (text) => (text === 1 ? '' : ''),
	},
	{
		title: '',
		dataIndex: 'operation',
		key: 'operation',
		width: 200,
		scopedSlots: { customRender: 'operation' },
		ellipsis: true,
	},
];

export default {
	props: ['data', 'page'],
	data() {
		return {
			columns,
		};
	},
	computed: {
		tableData() {
			return this.data.map((item) => ({
				...item,
				key: item.id,
			}));
		},
	},
	methods: {
		// 
		changePage(page) {
			this.$emit('changePage', page);
		},
		editProduct(record) {
			this.$emit('edit', record);
		},
		removeProduct(record) {
			this.$emit('remove', record);
		},
	},
};
</script>
