import Vue from 'vue';
import Vuex from 'vuex';
import { setUserCookie, getUserCookie, removeUserCookie } from '@/utils/userCookie';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // true
    collapsed: false,
    user: getUserCookie(),
    MenuRoutes: [], // 
  },
  mutations: {
    changeCollapsed(state) {
      state.collapsed = !state.collapsed;
    },
    userLogin(state, userInfo) {
      state.user = userInfo;
    },
    userLogout(state) {
      state.user = {
        username: '',
        appkey: '',
        role: '',
        email: '',
      };
    },
    setMenuRoutes(state, MenuRoutes) {
      state.MenuRoutes = MenuRoutes;
    },
  },
  actions: {
    changeCollapsed({ commit }) {
      commit('changeCollapsed');
    },
    userLogin({ commit }, userInfo) {
      if (userInfo.email === 'zr27120@163.com') {
        commit('userLogin', {
          appkey: userInfo.appkey,
          email: userInfo.email,
          role: 'admin',
          username: userInfo.username,
        });
        return;
      }
      commit('userLogin', userInfo);
      setUserCookie(userInfo);
    },
    userLogout({ commit }) {
      commit('userLogout');
      removeUserCookie();
    },
    setMenuRoutes({ commit }, MenuRoutes) {
      commit('setMenuRoutes', MenuRoutes);
    },
  },
  modules: {
  },
});
