<template>
  <div class="basic-detail">
    <a-form-model ref="ruleForm" :model="formData" :rules="rules"
        :label-col="{span: 6}" :wrapper-col="{span: 13}">
      <a-form-model-item label="" prop="title" required>
        <a-input v-model="formData.title" />
      </a-form-model-item>
      <a-form-model-item label="" prop="desc">
        <a-input v-model="formData.desc" />
      </a-form-model-item>
      <a-form-model-item label="" required prop="category">
        <a-select v-model="formData.category" placeholder="" @change="changeCategory">
          <a-select-option v-for="c in categoryList" :key="c.id" :value="c.id">
              {{c.name}}
            </a-select-option>
        </a-select>
        <a-select v-model="formData.c_item" placeholder="">
          <a-select-option v-for="c in categoryItems" :key="c" :value="c">
            {{c}}
        </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="" prop="tags" required>
        <a-select
          mode="tags"
          placeholder=""
          :default-value="['']"
          v-model="formData.tags"
        >
          <a-select-option
            value=""
          ></a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="" class="next-btn" :wrapperCol="{span: 22}">
          <a-button type="primary" @click="next"></a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
import categoryApi from '@/api/category';

export default {
	props: ['formData'],
	data() {
		return {
			rules: {},
			categoryList: [],
			categoryItems: [],
		};
	},
	created() {
		categoryApi.getCategoryList().then((res) => {
        this.categoryList = res.data.data;
        if (this.$route.params.id) {
           for (let i = 0; i < this.categoryList.length; i += 1) {
              if (this.categoryList[i].id === this.formData.category) {
                this.categoryItems = this.categoryList[i].c_items;
              }
           }
        }
		});
	},
	methods: {
		changeCategory() {
			for (let i = 0; i < this.categoryList.length; i += 1) {
				if (this.categoryList[i].id === this.formData.category) {
					this.categoryItems = this.categoryList[i].c_items;
				}
			}
		},
		next() {
			this.$refs.ruleForm.validate((valid) => {
				if (valid) {
				this.$emit('next');
				return true;
				}
				this.$message.error('!');
				return false;
			});
		},
	},
};
</script>

<style scoped lang="less">
.basic-detail-container {
	.basic-btn {
		text-align: center;
	}
}
</style>
