<template>
	<div class="search-container">
		<a-form-model
			layout="inline"
			:model="searchForm"
			@submit="handleSubmit"
			@submit.native.prevent
		>
			<a-form-model-item label="">
				<a-input
					v-model="searchForm.searchWord"
					placeholder=""
				></a-input>
			</a-form-model-item>
			<a-form-model-item label="" class="category">
				<a-select
					show-search
					placeholder=""
					style="width: 200px"
					@change="handleChange"
					allowClear
				>
					<template v-if="categoryList.length > 0">
						<a-select-option
							v-for="category in categoryList"
							:value="category.id"
							:key="category.id"
						>
							{{ category.name }}
						</a-select-option>
					</template>
				</a-select>
			</a-form-model-item>
			<a-form-model-item>
				<a-button type="primary" html-type="submit" class="btn">
					
				</a-button>
			</a-form-model-item>
		</a-form-model>
	</div>
</template>

<script>
export default {
  props: ['categoryList'],
	data() {
		return {
			searchForm: {
				searchWord: '',
				category: '',
			},
		};
	},
	methods: {
		handleSubmit() {
			this.$emit('submit', this.searchForm);
		},
		handleChange(value) {
			this.searchForm.category = value;
		},
	},
};
</script>

<style scoped lang='less'>
.search-container {
	padding: 10px 30px;

	.category {
		margin: 0 30px;
	}

	.btn {
		margin-top: 0;
		margin-left: 20px;
	}
}
</style>
